<template>
  <div>
    <van-calendar
      v-model="show2"
      color="#1890FF"
      type="range"
      :name="calendarName"
      :min-date="minDate"
      :max-date="maxDate"
      :default-date="defaultDate"
      @confirm="onConfirm"
    />
  </div>
</template>

<script>
export default {
  components: {

  },
  props: {
    // showCalendar: {
    //   type: null,
    //   required: true
    // },
    calendarName: {
      type: null,
      required: true
    }
  },
  data() {
    return {
      show2: false,
      kbsjbegin: '',
      kbsjend: '',
      minDate: new Date(1999, 0, 1),
      maxDate: new Date(2099, 0, 1),
      defaultDate: [new Date(), new Date(new Date().setDate(new Date().getDate() + 1))]
    }
  },
  computed: {
  },

  created() {
    const self = this
    this.$bus.$on('showCalendar', function(data) {
      console.log('searchClick=====aaa=======')
      self.show2 = true
    })
  },

  mounted() {
  },
  updated() {

  },
  methods: {
    onConfirm(date) {
      // const [start, end] = date
      this.show2 = false
      // this.kbsjbegin = this.formatDate8(start)
      // this.kbsjend = this.formatDate8(end)
      // this.date = `${this.formatDateShow(start)}-${this.formatDateShow(end)}`
      this.$emit('confirmCalendar', date)
      // this.search.kssj = parseInt(this.kbsjbegin)
      // this.search.jssj = parseInt(this.kbsjend)
    },
    formatDate8(date) {
      var month = date.getMonth() + 1
      var day = date.getDate()
      if (month < 10) {
        month = '0' + month
      }
      if (day < 10) {
        day = '0' + day
      }
      return `${date.getFullYear()}${month}${day}`
    },
    formatDateShow(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
    }
  }
}
</script>

